import { Component, OnInit, trigger, state, style, transition, animate } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';
@Component({
  selector: 'app-go-top',
  templateUrl: './go-top.component.html',
  styleUrls: ['./go-top.component.scss'],
  animations: [
    trigger('show', [
      state('block', style({
        'display' : 'block',
        'opacity' : '1'
      })),
      state('none', style({
        'display' : 'none',
        'opacity' : '0'
      })),
      transition('block => none', animate('600ms cubic-bezier(0.4, 0, 0.2, 1)')),
      transition('none => block', animate('600ms cubic-bezier(0.4, 0, 0.2, 1)'))
    ])
  ]
})
export class GoTopComponent implements OnInit {
  public goTop : any;

  constructor() { 
    this.goTop = 'none'
  }

  ngOnInit() {
  }

  goTopShow(): void{
    this.goTop = 'block'
  }

  goTopHide(): void{
    this.goTop = 'none'
  }

  // 回到顶层
  GoTop(): void{
    let timer = setInterval(function(){
      var osTop = document.documentElement.scrollTop || document.body.scrollTop;  
      var isSpeed = Math.floor(-osTop / 6);
      document.documentElement.scrollTop = document.body.scrollTop = osTop + isSpeed;
      if (osTop == 0) {
        clearInterval(timer);
      }
    },30);
  }

}



