import { Component, Renderer2, Input, Inject , ViewChild, ElementRef, OnInit, trigger, state, style, transition, animate } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';
import { Http, Response, RequestOptions, Headers, URLSearchParams  } from '@angular/http';
import { RouterModule, Router } from '@angular/router';

@Component({
  selector: 'app-alert-model',
  templateUrl: './alert-model.component.html',
  styleUrls: ['./alert-model.component.scss'],
  animations: [
    trigger('display', [
      state('none',style({
        'top': '-10%',
        'display' : 'none'
      })),
      state('block', style({
        'top' : '12%',
        'display' : 'block'
      })),
      transition('block => none', animate('600ms cubic-bezier(0.4, 0, 0.2, 1)')),
      transition('none => block', animate('600ms cubic-bezier(0.4, 0, 0.2, 1)'))
  ])]
})

export class AlertModelComponent implements OnInit {
  @Input() alertValue : String;
  public alertText : any;
  public alertModelShow : any;

  constructor() {
    this.alertModelShow = 'none'
    
   }

  ngOnInit() {
  }

  modelShow(value: any): void{
    this.alertText = value;
    this.alertModelShow = 'block';
  }

  modelHide(): void{
    this.alertModelShow = 'none';
  }

}



